import React, { Component, Fragment } from 'react'
import { AppSelector, Panel, Provider } from 'rhinoui'

export default class App extends Component {
  render () {
    return (
      <div>
        <Provider theme='dark' locale='ar'>
          <Fragment>
            <Panel>
                <AppSelector></AppSelector>
            </Panel>
            <Panel color='#'></Panel>
          </Fragment>
        </Provider>
      </div>
    )
  }
}



